<template>
  <Modal
    :active="showModal"
    :event-name="'file-view-modal'"
    :custom-background="'bg-transparent'"
    :custom-overlay="type === 'image' ? 'bg-transparent' : undefined"
    :full-screen="'ipad'"
    :custom-width="'w-full'"
    :custom-padding="'p-spacing-0'"
  >
    <div class="relative h-full w-full">
      <!-- Close Button -->
      <div
        class="absolute -left-spacing-500 -top-spacing-500 z-[100000] h-spacing-1000 w-spacing-1000 cursor-pointer rounded-full bg-bg-overlay hover:bg-alpha-black-80"
        aria-label="Close"
        :class="type === 'image' ? 'hidden' : ''"
        role="button"
        tabindex="0"
        @click="handleClose"
        @keyup.enter="handleClose"
      >
        <IconX
          class="absolute bottom-spacing-150 right-spacing-150 h-spacing-300 w-spacing-300 text-constant-neutral-white"
        />
      </div>

      <div
        class="flex h-full w-full flex-col items-center justify-center gap-spacing-100 text-body/l/relaxed/400 text-content-on-neutral-primary"
      >
        <!-- Image Preview with Viewer.js -->
        <div v-if="tempFileLink && type === 'image' && !isLoading">
          <img
            ref="imageViewer"
            :src="tempFileLink"
            class="invisible h-0 w-0"
            @load="initializeViewer"
            @error="handleImageError"
            alt="Preview Image"
          />
        </div>

        <!-- Document Preview -->
        <iframe
          v-else-if="tempFileLink && type === 'document' && !isLoading"
          :src="`https://docs.google.com/gview?url=${tempFileLink}&embedded=true`"
          :class="isIFrameLoading ? 'h-0 w-0' : 'h-full w-full'"
          @load="handleIframeLoad"
          @error="handleIframeError"
        />

        <!-- Loader -->
        <div
          v-if="isLoading || isIFrameLoading"
          class="h-spacing-400 w-spacing-400 animate-spin text-constant-brand-primary"
        >
          <IconLoader2 />
        </div>

        <!-- Error State -->
        <IconFileQuestion
          v-if="error"
          class="h-spacing-600 w-spacing-600 text-content-on-neutral-tertiary"
        />
        <p
          v-if="error"
          class="text-body/l/relaxed/400 text-content-on-neutral-secondary"
        >
          {{ error }}
        </p>
      </div>
    </div>
  </Modal>
</template>

<script>
import Modal from "@/components/General/Modal.vue";
import { FILE_VALIDATION_OBJECT } from "@/utilities/constants/constants";
import { mapActions } from "vuex";
import Viewer from "viewerjs";
import "viewerjs/dist/viewer.css";

export default {
  name: "FileViewerModal",
  components: {
    Modal,
    IconX: () => import("@/components/svgIcons/x.vue"),
    IconFileQuestion: () => import("@/components/svgIcons/file-question.vue"),
    IconLoader2: () => import("@/components/svgIcons/loader-2.vue"),
  },
  data() {
    return {
      showModal: false,
      isLoading: false,
      tempFileLink: null,
      isIFrameLoading: false,
      type: null,
      error: null,
      modalHandler: null,
      viewer: null, // Viewer.js instance
    };
  },
  mounted() {
    this.modalHandler = (data) => {
      if (data?.showModal) {
        this.showModal = true;
        if (data?.fileUrl) {
          const normalizedUrl = data?.fileUrl?.toLowerCase();
          if (this.isImage(normalizedUrl)) {
            this.type = "image";
          } else if (this.isDocument(normalizedUrl)) {
            this.type = "document";
            this.isIFrameLoading = true; // Show loader for document preview
          } else {
            this.error = "File format not supported.";
            return;
          }
          this.fetchTempLink(data.fileUrl);
        } else if (data?.tempFileLink) {
          this.tempFileLink = data?.tempFileLink;
          this.initializeViewer();
        }
      } else {
        this.closeModal();
      }
    };
    this.$bus.$on("file-view-modal", this.modalHandler);
  },
  methods: {
    ...mapActions("utils", ["getTempFileViewLink"]),

    // Helper to detect image formats
    isImage(fileUrl) {
      return FILE_VALIDATION_OBJECT?.EXTENSIONS?.IMAGE?.some((ext) =>
        fileUrl?.endsWith(ext),
      );
    },

    // Helper to detect document formats
    isDocument(fileUrl) {
      return FILE_VALIDATION_OBJECT?.EXTENSIONS?.DOC?.some((ext) =>
        fileUrl?.endsWith(ext),
      );
    },

    // Close modal and reset state
    handleClose() {
      this.$bus.$emit("file-view-modal", { showModal: false });
      this.destroyViewer();
    },

    // Fetch temporary file view link
    async fetchTempLink(fileUrl) {
      this.isLoading = true;
      this.error = null;
      this.tempFileLink = null;

      const res = await this.getTempFileViewLink({
        params: {
          fileUrl,
        },
      });
      if (res?.success) {
        this.tempFileLink = res.filePath;
        if (this.type === "image") {
          this.initializeViewer();
        }
      } else {
        this.error = "Unable to generate preview for this file.";
      }
      this.isLoading = false;
    },

    // Handle iframe load (hide loader when iframe is fully loaded)
    handleIframeLoad() {
      this.isIFrameLoading = false;
    },

    // Handle iframe error (in case the file fails to load)
    handleIframeError() {
      this.error = "Failed to load the document.";
      this.isIFrameLoading = false;
    },

    // Handle image error
    handleImageError() {
      this.error = "Failed to load the image.";
    },

    // Initialize Viewer.js
    initializeViewer() {
      var self = this;
      if (this.viewer) return; // Avoid re-initializing if viewer already exists
      this.$nextTick(() => {
        if (this.$refs.imageViewer) {
          this.viewer = new Viewer(this.$refs.imageViewer, {
            button: true,
            navbar: true,
            title: true,
            toolbar: true,
            tooltip: true,
            movable: true,
            zoomable: true,
            rotatable: true,
            scalable: true,
            transition: true,
            fullscreen: true,
            keyboard: true,
            url: "src",
            play: true,
            zIndex: 10000000000,
            hidden() {
              self.handleClose();
            },
          });
          this.viewer.show();
        }
      });
    },

    // Destroy Viewer.js instance
    destroyViewer() {
      if (this.viewer) {
        this.viewer.destroy();
        this.viewer = null;
      }
    },

    // Close modal and reset state
    closeModal() {
      this.showModal = false;
      this.error = null;
      this.isLoading = false;
      this.tempFileLink = null;
      this.isIFrameLoading = false;
      this.type = null;
      this.destroyViewer();
    },
  },

  beforeDestroy() {
    this.$bus.$off("file-view-modal", this.modalHandler);
    this.destroyViewer();
    this.modalHandler = null;
  },
};
</script>
